export default {
	components: {
		uniRate
	},
	data() {
		return {
			navList: [{
					id: 0,
					logo: "http://qakj5dvcb.bkt.clouddn.com/static/shopCenter_icon_a.png",
					name: "商家资料",
					desc: "备案信息 更改上传资料等",
					status: 0
				},
				{
					id: 1,
					logo: "http://qakj5dvcb.bkt.clouddn.com/static/shopCenter_icon_b.png",
					name: "顾客留言",
