						url: "/pages/shop/leaveWord"
					})
					break
				case "系统消息":
					uni.navigateTo({
						url: "/pages/user/msg"
					})
					break
				case "店铺二维码":
					uni.navigateTo({
						url: "/pages/shop/QRCode"
					})
					break
